.header-shopping {
  margin-bottom: 40px;
  color: var(--header-border-color);
  font-size: 32px;
  line-height: 1.19;
  letter-spacing: -1.28px;
}

.header-color {
  color: #4f2ee8;
  font-size: 32px;
  line-height: 38px;
  letter-spacing: -1.28px;
}

.shopping-list-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.shopping-list-item {
  display: flex;
  align-items: center;
  padding: 14px;
  margin: 0;

  max-width: 335px;
  height: 170px;

  border-radius: 16px;
  border: 2px solid var(--book-border-color);
  background: var(--background-color);

  transition-property: box-shadow;
  transition: var(--transition);
}

.shopping-list-item:hover,
.shopping-list-item:focus {
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}

.shopping-list-item:hover .shopping-list-img,
.shopping-list-item:focus .shopping-list-img {
  border-color: var(--logo-fill-color);
}

.sh-wrapper {
  width: 100px;
  height: 142px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.book-logo {
  width: 32px;
  height: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: var(--violet);
}

.shopping-list-img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 8px;
  background: lightgray 50% / cover no-repeat;
  border: 1px solid transparent;
  transition-property: border-color;
  transition: var(--transition);
}

.trash-btn {
  position: absolute;
  top: 2px;
  right: -2px;
  width: 28px;
  height: 28px;
  border: transparent;
  padding: 6px;

  background-color: var(--violet);
  border-radius: 50%;
  transition-property: opacity;
  transition: var(--transition);
}

.trash-btn:hover{
  opacity: 0.85;
}

.trash-logo {
  width: 20px;
  height: 20px;
  fill: var(--white);
}

.shopping-list-info {
  margin-left: 14px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.shopping-book-title {
  margin: 0 auto 4px 0;
  max-width: 144px;

  font-size: 16px;
  line-height: 1.12;
  letter-spacing: -0.64px;
  text-transform: uppercase;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.shopping-book-polygraphy {
  margin: 0 auto 8px 0;
  max-width: 144px;
  color: var(--books-color);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.17;
  letter-spacing: 0.36px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.shopping-book-author {
  max-width: 100px;
  font-size: 12px;
  font-style: italic;
  line-height: 1.17;
  letter-spacing: 0.36px;
  color: var(--books-color);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.sh-desc-wrapper {
  width: 193px;
  height: 68px;
  margin: 0 auto 14px 0;
}

.shopping-book-desc {
  width: 100%;

  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
  text-overflow: ellipsis;

  color: var(--text);
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.28;
  letter-spacing: -0.56px;
}

.sh-buy-wrapper {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}

.links-to-buy {
  display: flex;
  justify-content: center;
  align-items: baseline;
  gap: 8px;
}

.item-to-buy {
  height: 16px;
  width: 100%;
}

.shop-logo-applebook,
.shop-logo-bookshop,
.shop-logo-amazon {
  height: 16px;
  width: 16px;
  transition-property: filter;
  transition: var(--transition);
}

.shop-logo-amazon {
  width: 32px;
  height: 11px;
  fill: var(--text);
}

.shop-logo-amazon:hover,
.shop-logo-applebook:hover,
.shop-logo-bookshop:hover {
  filter: drop-shadow(0px 3px 3px rgba(0, 0, 0, 0.15));
}

.empty-sh-page {
  padding-top: 80px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.empty-sh-page-text {
  width: 244px;
  color: var(--shopping-list-empty-text-color);
  text-align: center;
  font-size: 14px;
  line-height: 1.28;
  letter-spacing: -0.28px;
}

.empty-sh-page-img {
  width: 265px;
  height: 198px;
}

@media screen and (min-width: 768px) {
  .header-shopping,
  .header-color {
    font-size: 48px;
    line-height: 1.08;
    letter-spacing: -1.92px;
  }

  .shopping-book-title {
    max-width: 400px;
  }

  .shopping-list-item {
    max-width: 704px;
    height: 213px;
    padding: 24px;
  }

  .shopping-book-polygraphy {
    max-width: 400px;
    margin-bottom: 14px;
  }

  .shopping-list-info {
    margin-left: 24px;
    width: 516px;
    height: 100%;
  }

  .shopping-book-author {
    max-width: 300px;
  }

  .sh-desc-wrapper {
    width: 508px;
    height: 53px;
  }

  .sh-wrapper {
    width: 116px;
    height: 165px;
  }

  .sh-buy-wrapper {
    height: 30px;
  }

  .trash-btn {
    top: 0px;
    right: 0px;
    padding: 6px;
    width: 34px;
    height: 34px;
  }

  .trash-logo {
    width: 28px;
    height: 28px;
  }

  .links-to-buy {
    gap: 16px;
  }

  .shop-logo-amazon {
    width: 48px;
    height: 15px;
  }

  .shop-logo-applebook {
    width: 28px;
    height: 27px;
  }

  .shop-logo-bookshop {
    width: 32px;
    height: 30px;
  }

  .empty-sh-page {
    padding-top: 100px;
    width: 349px;
  }

  .empty-sh-page-text {
    width: 100%;
    font-size: 18px;
    line-height: 1.33;
    letter-spacing: -0.36px;
  }

  .empty-sh-page-img {
    width: 322px;
    height: 241px;
  }
}

@media screen and (min-width: 1440px) {
  .shopping-list-item {
    max-width: 996px;
    height: 213px;
  }

  .shopping-book-title {
    max-width: 600px;
  }

  .shopping-list-info {
    width: 808px;
  }

  .sh-desc-wrapper {
    width: 808px;
    height: 53px;
  }
}
