  height: 44px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.22;
  display: flex;
}

.js-pagination-pages:is(:hover, :focus) {
  scale: 1.15;
}

.js-three-point {
  color: var(--text);
  border: 1px solid var(--pagination-border-color);
  cursor: pointer;
  transition: var(--transition);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.22;
